import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class ProductItemMng extends Component {
	constructor(props) {
		super(props);
		this.state = {
			isShowAllDesc: false
		};
	}

	toSlug = (str) => {
		// Chuyn ht sang ch thng
		str = str.toLowerCase();

		// xa du
		str = str.replace(/(||||||||||||||||)/g, 'a');
		str = str.replace(/(||||||||||)/g, 'e');
		str = str.replace(/(||||)/g, 'i');
		str = str.replace(/(||||||||||||||||)/g, 'o');
		str = str.replace(/(||||||||||)/g, 'u');
		str = str.replace(/(||||)/g, 'y');
		str = str.replace(/()/g, 'd');

		// Xa k t c bit
		str = str.replace(/([^0-9a-z-\s])/g, '');

		// Xa khong trng thay bng k t -
		str = str.replace(/(\s+)/g, '-');

		// xa phn d -  u
		str = str.replace(/^-+/g, '');

		// xa phn d -  cui
		str = str.replace(/-+$/g, '');

		// return
		return str;
	}

	onDelete = (product) => {
		//Thm dng comment >>>eslint-disable-line<<< ngay trn dng c hm confirm  chy c hm ny
		if(confirm('Bn chc chn mun xa?')) { //eslint-disable-line
			this.props.onDelete(product);
		}
	}

	showDiscount = (discount) => {
		if(discount > 0) {
			return ("-" + discount + "%");
		} else {
			return (
				<span className="alert alert-info mb-0" role="alert">Khng gim gi</span>
			);
		}
	}

	showInventory = (inventory) => {
		if(inventory === 0) {
			return (
				<span className="alert alert-danger mb-0" role="alert">Ht hng</span>
			);
		} else {
			return inventory;
		}
	}

	shortenParagraph = (str, maxLength = 50, separator = " ") => {
		return (str.length <= maxLength) ? str : str.substr(0, str.lastIndexOf(separator, maxLength)) + "...";
	}

	onShowMoreDescription = (description) => {
		if (description === '') {
			return (
				<td className="col_description text-center">
					<span className="alert alert-info mb-0" role="alert">Cha c m t</span>
				</td>
			);
		} else {
			return (
				<td className="col_description">
					{ this.state.isShowAllDesc ? description : this.shortenParagraph(description, 60, " ") }
					<button onClick={ () => this.setState({ isShowAllDesc: !this.state.isShowAllDesc }) } className="btn_readmore">
						{ this.state.isShowAllDesc ? 'Thu gn' : 'Xem thm' }
					</button>
				</td>
			);
		}
	}

	render() {
		var { product, index } = this.props;
		var toSlugName = this.toSlug(product.name);
		// console.log(product);

		return (
			<tr>
				<td className="col_order text-center">{index + 1}</td>
				<td className="col_photo text-center">
					<Link to={`/product-detail/${toSlugName}.${product.id}`} className="over">
						<img src={product.photo} alt={product.name} width="80" />
					</Link>
				</td>
				<td className="col_name">
					<Link to={`/product-detail/${toSlugName}.${product.id}`} className="product_title">
						{product.name}
					</Link>
				</td>
				<td className="col_price_org text-center">
					{product.price_original}
				</td>
				<td className="col_discount text-center ws_nowrap">
					{this.showDiscount(product.discount)}
				</td>
				<td className="col_price_sale text-center">
					{parseInt(product.price_original*(100-product.discount)/100)}
				</td>
				<td className="col_rating text-center">
					{product.rating}
				</td>
				{this.onShowMoreDescription(product.description)}
				<td className="col_trademark text-center">
					{product.trademark}
				</td>
				<td className="col_inventory text-center ws_nowrap">
					{this.showInventory(product.inventory)}
				</td>
				<td className="col_count_view text-center ws_nowrap">
					{product.count_view}
				</td>
				<td className="col_action text-center">
					<Link to={`/product-mng/${product.id}/edit`} className="btn btn-success">
						<i className="fa fa-pencil mr-5"></i>Sa
					</Link>&nbsp;
					<button
						type="button"
						className="btn btn-danger"
						onClick={ () => this.onDelete(product) }
					>
						<i className="fa fa-trash mr-5"></i>Xa
					</button>
				</td>
			</tr>
		);
	}
}

export default ProductItemMng;
