import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class CartDropdownItem extends Component {
	toSlug = (str) => {
		// Chuyn ht sang ch thng
		str = str.toLowerCase();

		// xa du
		str = str.replace(/(||||||||||||||||)/g, 'a');
		str = str.replace(/(||||||||||)/g, 'e');
		str = str.replace(/(||||)/g, 'i');
		str = str.replace(/(||||||||||||||||)/g, 'o');
		str = str.replace(/(||||||||||)/g, 'u');
		str = str.replace(/(||||)/g, 'y');
		str = str.replace(/()/g, 'd');

		// Xa k t c bit
		str = str.replace(/([^0-9a-z-\s])/g, '');

		// Xa khong trng thay bng k t -
		str = str.replace(/(\s+)/g, '-');

		// xa phn d -  u
		str = str.replace(/^-+/g, '');

		// xa phn d -  cui
		str = str.replace(/-+$/g, '');

		// return
		return str;
	}

	showPrice = (price_original, discount) => {
		if(discount > 0) {
			return (
				<p className="product_price">
					<strong className="price_sale">{parseInt(price_original*(100-discount)/100)}</strong><br/>
					<strike className="price_original"><small>{price_original}</small></strike>&nbsp;
					<span className="discount label label-danger">-{discount}%</span>
				</p>
			);
		} else {
			return (
				<p className="product_price">
					<strong className="price_sale">{parseInt(price_original*(100-discount)/100)}</strong><br/>
				</p>
			);
		}
	}

	onDeleteProductInCart = (id) => {
		//Thm dng comment >>>eslint-disable-line<<< ngay trn dng c hm confirm  chy c hm ny
		if(confirm('Bn chc chn mun xa?')) { //eslint-disable-line
			this.props.onDeleteProductInCart(id);
		}
	}

	render() {
		var { item } = this.props;
		var toSlugName = this.toSlug(item.product.name);
		// console.log(item);

		return (
			<li className="product_item">
				<div className="row">
					<div className="col-sm-3 pr-0">
						<Link to={`/product-detail/${toSlugName}.${item.product.id}`}
							className="product_thumb over"
							onClick={ () => { document.getElementById('dropdown_carts').classList.remove('open') } }
						>
							<img src={item.product.photo} alt={item.product.name} />
						</Link>
					</div>
					<div className="col-sm-9">
						<p className="product_title">
							<Link
								to={`/product-detail/${toSlugName}.${item.product.id}`}
								onClick={ () => { document.getElementById('dropdown_carts').classList.remove('open') } }
							>
								{item.product.name}
							</Link>
						</p>
						{this.showPrice(item.product.price_original, item.product.discount)}
					</div>
				</div>
				<div className="product_quantity">x{item.quantity}</div>
				<button
					type="button"
					className="btn_delete"
					data-toggle="tooltip"
					data-placement="left"
					data-original-title="Xa sn phm"
					onClick = { () => this.onDeleteProductInCart(item) }
				>
					<i className="fa fa-trash"></i>
				</button>
			</li>
		);
	}
}

export default CartDropdownItem;
